@media all and (max-width: 420px) {
    .main-block-img {
        width: 10%;
        top: 20%;
        &-2 {
            width: 60%;
            top: 60%;
            height: 40%;
            left: 20%;
        }
    }
    .title {
        line-height: 40px;
    }
    .footer-list {
        width: 100% !important;
        text-align: center;
        margin: 20px 0 !important;
        &-3,
        &-4 {
            max-width: 100%;
        }
    }
    .footer-soc {
        // grid-template-columns: repeat(5, 1fr);
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &-title {
            width: 100%;
            // grid-column: 1/6;
            text-align: center;
        }
    }
}
