.header {
    padding: 24px 0;
    &-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &-left {
        display: flex;
        li {
            a {
                font-size: 14px;
                line-height: 18px;
                /* identical to box height, or 130% */

                color: #000000;
            }
            &:not(last-child) {
                margin: 0 30px 0 0;
            }
        }
    }
    &-right {
        display: flex;
        align-items: center;
        a {
            font-size: 14px;
            line-height: 18px;
            color: #6f1eaa;
            margin-right: 33px;
        }
    }
}
.button {
    background: #6f1eaa;
    border-radius: 19px;
    font-weight: bold;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 1px;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    height: 38px;
    &-center {
        margin: 0 auto;
    }
}
.burger {
    &-click {
        display: none;
    }
}
@media all and (max-width: 950px) {
    .header {
        padding-top: 20px;
        transition: all 0.5s ease-in-out;
        &-container {
            justify-content: space-around;
        }
        &-active {
            background: #fef7e5;
            padding-bottom: 100%;
            .header-left {
                max-height: 1000px;
            }
            .burger-click {
                &::after {
                    transform: rotate(45deg);
                    transform-origin: 0 0;
                    border-bottom: transparent;
                }
                &::before {
                    transform-origin: 0 0;

                    transform: rotate(-45deg);
                }
            }
        }
        &-left {
            flex-direction: column;
            max-height: 0;
            overflow: hidden;
            transition: all 0.5s ease-in-out;
            li,
            .li {
                margin: 25px 0 !important;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 36px;
                a {
                    font-size: 36px;
                }
            }
        }
        .button {
            margin: 25px auto;
            font-size: 36px;
            width: max-content;
            height: 50px;
            padding: 20px;
        }
    }
    .burger {
        &-click {
            display: block;
            width: 60px;
            height: 35px;
            position: relative;
            transition: all 0.5s ease-in-out;
            &::after {
                display: block;
                width: 60px;
                height: 15px;
                border-top: 1px solid #000;
                border-bottom: 1px solid #000;
                position: absolute;
                right: 0;
                top: 0;
                transition: all 0.5s ease-in-out;
                content: "";
            }
            &::before {
                content: "";
                transition: all 0.5s ease-in-out;
                display: block;
                width: 60px;
                border-top: 1px solid #000;
                bottom: 0;
                right: 0;
                position: absolute;
            }
        }
    }
}
