// Подключение своих SCSS файлов
@import "my";
@import "fonts";
@import "header";
.wrapper {
    max-width: 1440px;
    margin: 0 auto;
}
body {
    font-family: "Roboto";
}
.container {
    max-width: 1220px;
    margin: 0 auto;
}
.title {
    font-size: 44px;
    line-height: 53px;
    color: #000000;
    &-p {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        color: #6f1eaa;
        margin: 0 0 20px 0;
    }
}
.main {
    &-block {
        position: relative;
        background: #fef7e5;
        padding: 102px 0 240px;
        &-img {
            position: absolute;
            &-1 {
                left: 0;
                top: 0;
            }
            &-2 {
                bottom: 10%;
                left: 50%;
            }
            &-3 {
                right: 0;
                top: 0;
            }
        }
    }
    &-content {
        max-width: 565px;
        margin: 0 auto;
        z-index: 11;
        &-button {
            margin: 0 auto;
        }
        p {
            text-align: center;
        }
        &-title {
            font-size: 55px;
            line-height: 67px;
            margin: 0 0 32px 0;
        }
        &-text {
            font-size: 20px;
            line-height: 28px;
            color: #000000;
            margin: 0 0 23px 0;
        }
    }
    &-info {
        display: flex;
        flex-wrap: wrap;
        margin-top: 60px;
        justify-content: center;
        &-side {
            &-block {
                margin: 0px 0 60px;
                &-text {
                }
            }
            &-title {
                margin: 0;
            }
            &-left {
                max-width: 527px;
            }
            &-right {
                max-width: 470px;
                margin-left: 200px;
            }
        }
    }
}
.text {
    font-size: 16px;
    line-height: 23px;
    color: #000000;
}
.p_s {
    font-size: 15px;
    line-height: 23px;
    color: #000000;
    margin: 5px 0 0;
}
.after {
    &-border {
        position: relative;

        &::after,
        &::before {
            content: "";
            display: block;
            width: 100%;
            height: 1px;
            background: #f7a500;
            position: absolute;
        }
        &::after {
            top: -30px;
        }
        &::before {
            bottom: -40px;
        }
    }
}
.b_text {
    color: #6f1eaa;
}
.how {
    background: #faf9f6;
    border-radius: 0px;
    padding: 60px 0;
    margin: 45px 0 0;
    &-title {
        text-align: center;
        align-self: flex-end;
    }
    &-blocks {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        &-block {
            max-width: 360px;
            min-height: 355px;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 20px 15px;
            &-img {
                align-self: center;
                width: 192px;
                height: 192px;
                border-radius: 50%;
                background: #fff7c2;
                display: grid;
                justify-items: center;
                align-items: center;
            }
        }
    }
    &-content {
        align-self: flex-end;
    }
    &-text {
        text-align: center;
        font-size: 14px;
        line-height: 23px;
        color: #000000;
    }
}
.support {
    background: #f6f3fa;
    img {
        width: 100%;
    }
    &-blocks {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        &-right {
            max-width: 527px;
            margin: auto;
        }
    }
    &-title {
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.96px;
        text-transform: uppercase;
        color: #f7a500;
        &-2 {
            margin: 0 0 43px;
        }
    }
    &-text {
        margin-bottom: 26px;
    }
}
.pets {
    margin-top: 60px;
    position: relative;
    &-img {
        position: absolute;
        right: 50px;
        top: 20px;
        z-index: 1;
    }
    &-title {
        font-weight: bold;
        font-size: 43px;
        line-height: 53px;
        text-align: center;
        color: #000000;
        margin: 0;
        &-2 {
            font-size: 20px;
            line-height: 33px;
            color: #000000;
            text-align: center;
            margin: 24px 0;
        }
    }
    &-blocks {
        &-block {
            width: 1072px !important;
            background: #dff5f6;
            border-radius: 20px;
            margin: 27px auto;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            padding: 58px 0 58px 40px;
            &-right {
                max-width: 709px;
                margin-left: 40px;
            }
        }
        &-text {
            font-size: 16px;
            line-height: 26px;
            color: #000000;
        }
        &-soc {
            &-text {
                font-size: 20px;
                line-height: 33px;
                color: #000000;
            }
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            span {
                margin-left: 15px;
            }
        }
    }
}
.wild {
    padding: 60px 0;
    background: #faf9f6;
    margin-top: 150px;
    &-content {
        max-width: 820px;
        margin: 0 auto;
    }
    &-title {
        text-align: center;
        margin: 0 0 30px 0;
        &-2 {
            text-align: center;
            margin: 0 auto;
            max-width: 455px;
            font-weight: 500;
            font-size: 13px;
            line-height: 23px;
            color: #000000;
        }
    }
    &-blocks {
        margin: 70px 0 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &-block {
        max-width: 385px;
        background: #ffffff;
        display: flex;
        flex-wrap: wrap;
        &-top {
            height: max-content;
            img {
                width: 100%;
            }
        }
        &-bottom {
            box-sizing: border-box;
            padding: 30px;
            &-pad {
                padding-top: 30px;
            }
        }
        &-title {
            font-size: 24px;
            line-height: 32px;
            color: #000000;
            margin: 15px 0;
            &-orange {
                font-size: 15px;
                line-height: 23px;
                color: #f7a500;
                margin: 0 0;
            }
        }
        &-text {
            font-size: 11px;
            line-height: 23px;
            color: #000000;
            margin: 0 0 27px;
        }
        &-a {
            font-size: 16px;
            line-height: 19px;
            color: #6f1eaa;
        }
    }
}
.m {
    &-r {
        margin-right: 32px;
    }
    &-b {
        margin-bottom: 60px;
    }
}
@import "slick";
@import "footer";
@import "adaptive";
