@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 500px) {
    .footer-bottom {
        margin: 60px 0;
        max-width: 100%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    .burger-click {
        width: 30px;
        height: 22px;
        &::after {
            height: 9px;
            width: 30px;
        }
        &::before {
            width: 30px;
        }
    }
    .header-left {
        li,
        .li {
            font-size: 16px;
            margin: 12px 0 !important;
            a {
                font-size: 16px;
            }
        }
        button {
            margin-top: 12px !important;
            font-size: 16px !important;
        }
    }
}
@media all and (max-width: 860px) {
    .main-block-img {
        width: 10%;
        top: 20%;
        &-2 {
            width: 70%;
            top: 50%;
            height: 50%;
            left: 20%;
        }
    }
}
@media all and (max-width: 700px) {
    .main-content {
        width: 80%;
        &-title {
            font-size: 30px;
            line-height: 50px;
        }
        &-text {
            font-size: 18px;
        }
    }
}
@media all and (max-width: 600px) {
    .title {
        font-size: 30px !important;
    }
    .text {
        font-size: 18px;
    }
    .pets-blocks-block {
        padding: 60px 0;
    }
}
@media all and (max-width: 840px) {
    .pets-img {
        display: none;
    }
}
@media all and (max-width: 800px) {
    .m {
        &-r {
            margin: 0 0 50px 0;
        }
    }
}
