.footer {
    background: #48156d;
    &-top {
        background: #6f1eaa;
    }
    &-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 30px 0;
    }
    &-center {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding-top: 60px;
    }
    &-list {
        margin: 0 50px;
        &-1 {
            margin-left: 190px;
        }
        &-3 {
            max-width: 108px;
        }
        &-4 {
            max-width: 152px;
        }
        &-title {
            margin: 15px 0;
            a {
                font-size: 12px !important;
                line-height: 14px !important;
                letter-spacing: 0.6px !important;
                text-transform: uppercase;
                color: #c282f0 !important;
            }
        }
        li {
            margin: 6px 0;
            a {
                font-size: 14px;
                line-height: 18px;
                color: #ffffff;
            }
        }
    }
    &-soc {
        display: grid;
        flex-wrap: wrap;
        margin-top: 0;
        grid-template-columns: repeat(3, 1fr);
        height: max-content;
        &-title {
            margin: 0 0 16px !important;
            grid-column: 1/4;
        }
        li {
            margin: 0px 30px 15px 0px;
            height: max-content;
        }
    }
    &-bottom {
        margin-top: 60px;
        margin-left: 21%;
        a {
            font-size: 14px;
            line-height: 18px;
            color: #ffffff;
            &:not(:first-child) {
                margin-left: 15px;
            }
        }
    }
}
